import styled from "styled-components";

/* Data fetching */
import { useQuery } from "@tanstack/react-query";
import {
  IGetMoviesResult,
  getNowPlayingMovies,
  getTopRatedMovies,
  getUpcomingMovies,
} from "../api";

/* Components */
import Banner from "../Components/Banner";
import Slider from "../Components/Slider";

/* Icons */
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";

/* Components Styling */
const Loader = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 5vw;
  h1 {
    margin-top: 2vw;
    font-size: 2vw;
  }
`;

const SliderWrapper = styled.div`
  // Move up Slider
  position: relative;
  top: -15vw;
`;

function Home() {
  // Data-fectching
  const useMultipleQuery = () => {
    const nowPlaying = useQuery<IGetMoviesResult>(["nowPlaying"], getNowPlayingMovies);
    const topRated = useQuery<IGetMoviesResult>(["topRated"], getTopRatedMovies);
    const upcoming = useQuery<IGetMoviesResult>(["upcoming"], getUpcomingMovies);
    return [nowPlaying, topRated, upcoming];
  };

  const [
    { isLoading: loadingNowPlaying, data: nowPlayingData },
    { isLoading: loadingTopRated, data: topRatedData },
    { isLoading: loadingUpcoming, data: upcomingData },
  ] = useMultipleQuery();

  const isLoading = loadingNowPlaying || loadingTopRated || loadingUpcoming;

  return (
    <>
      {isLoading ? (
        <Loader>
          <FontAwesomeIcon icon={faSpinner} color="red" spinPulse />
          <h1> </h1>
        </Loader>
      ) : (
        <>
          <Banner movie={nowPlayingData?.results[6]} category="" />
          <SliderWrapper>
            <Slider movies={nowPlayingData?.results} title="   " category="" />
            <Slider
              movies={topRatedData?.results}
              title="  TOP 10 "
              category=""
            />
            <Slider movies={upcomingData?.results} title="  " category="" />
          </SliderWrapper>
        </>
      )}
    </>
  );
}

export default Home;
